@keyframes ripple {
  from {
    opacity: 1;
    transform: scale(0);
  }

  to {
    opacity: 0;
    transform: scale(10);
  }
}

._ripple:after {
  content: "";
  width: 100px;
  height: 100px;
  opacity: 0;
  background-color: #fff3;
  border-radius: 50%;
  margin-top: -50px;
  margin-left: -50px;
  animation: .8s ripple;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
}

._ripple:focus:not(:active):after {
  display: block;
}

